import Vue from 'vue'
import Router from 'vue-router'
import store from './store'
import Home from './views/Home.vue'

Vue.use(Router)

const router = new Router({
    routes: [
        {
            path: '/',
            name: 'home',
            component: Home
        },
        {
            path: '/read',
            name: 'read',
            component: () => import('./views/Read.vue')
        },
        {
            path: '/detail',
            name: 'detail',
            component: () => import('./views/Detail.vue')
        }
    ]
})

router.beforeEach((to, from, next) => {
    if (sessionStorage.getItem("user") && sessionStorage.getItem("user").length > 0) {
        store.commit('SET_USER', JSON.parse(sessionStorage.getItem("user")))
    }
    if (sessionStorage.getItem("token") && sessionStorage.getItem("token").length > 0) {
        store.commit('SET_TOKEN', sessionStorage.getItem("token"))
    }
    if (to.name !== 'read') {
        if (JSON.stringify(store.state.user) === '{}') {
            router.push('/read')
        } else {
            next();
        }
    }
    if(to.name === 'read') {
        if (JSON.stringify(store.state.user) !== '{}') {
            router.push('/')
        } else {
            next();
        }
    }
})
export default router